'use client';

import { MoreVertical } from "lucide-react";
import { useState, useEffect } from "react";

export default function ContactMessagesPage() {
  
  const [msgs, setMsgs] = useState([]);

  const getMsgs = async (pathExt='') => {
    const res = await fetch(`${END_POINT.CONTACT}${pathExt}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      },
      credentials: 'include',
    });
    const json = await res.json();
    setMsgs(json.data);
  };

  useEffect(() => {
    getMsgs('/new');
  }, []);

  return (
    <div className="p-8 flex-1">

      {/* PAGE HEADER */}
      <div className="flex flex-col sm:flex-row sm:items-center sm:justify-between gap-4 mb-10">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Contact Messages</h1>
          <p className="text-gray-500 mt-1">Manage all customer messages sent via the contact form.</p>
        </div>
      </div>

      {/* SEARCH + FILTERS */}
      <div className="bg-white p-5 rounded-xl border shadow-sm mb-8 flex flex-col md:flex-row md:items-center md:justify-between gap-4">
        <input
          type="text"
          placeholder="Search messages..."
          className="w-full md:w-72 px-4 py-2.5 border rounded-xl focus:ring-2 focus:ring-black outline-none transition"
        />

        <select className="px-4 py-2.5 border rounded-xl text-gray-700 focus:ring-2 focus:ring-black outline-none">
          <option value="">All Status</option>
          <option>Read</option>
          <option>Unread</option>
        </select>
      </div>

      {/* MESSAGES TABLE */}
      <div className="bg-white border rounded-xl shadow-sm overflow-x-auto">
        <table className="w-full min-w-[700px]">
          <thead className="bg-gray-50 border-b">
            <tr>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Name</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Email</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Subject</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Message</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Date</th>
              <th className="text-left px-6 py-3 text-sm font-medium text-gray-600">Status</th>
              <th className="text-right px-6 py-3 text-sm font-medium text-gray-600">Actions</th>
            </tr>
          </thead>

          <tbody>
            {msgs?.map((msg) => (
              <tr key={msg.id} className="border-b last:border-none hover:bg-gray-50 transition">
                <td className="px-6 py-4 font-semibold text-gray-900">{msg.name}</td>
                <td className="px-6 py-4 text-gray-700">{msg.email}</td>
                <td className="px-6 py-4 text-gray-700">{msg.subject}</td>
                <td className="px-6 py-4 text-gray-700 truncate max-w-xs">{msg.message}</td>
                <td className="px-6 py-4 text-gray-700">{msg.date}</td>
                <td className="px-6 py-4">
                  <span
                    className={`px-2.5 py-1 text-xs font-medium rounded-lg ${
                      msg.status === "Read" ? "bg-green-100 text-green-700" : "bg-yellow-100 text-yellow-700"
                    }`}
                  >
                    {msg.status}
                  </span>
                </td>
                <td className="px-6 py-4 text-right">
                  <button className="p-2 hover:bg-gray-200 rounded-full transition">
                    <MoreVertical size={18} />
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

    </div>
  );
}
